import { ADD_ITEM } from "../constants";
import { DELETE_ITEM } from "../constants";
import { CLEAR_ITEMS } from "../constants";

export const addItem = (text, dueDate) => {
    const action = {
        type: ADD_ITEM,
        text: text,
        dueDate: dueDate
    }
    console.log('action in addItem', action);
    return action;
}

export const deleteItem = (id) => {
    const action = {
        type: DELETE_ITEM,
        id: id
    }
    console.log('deleting in actions', action);
    return action;
}

export const clearItems = () => {
    const action = {
        type: CLEAR_ITEMS
    }
    return action;
}


