import React from 'react';
import { connect } from 'react-redux';
import { addItem } from "../actions/index";
import { deleteItem } from "../actions/index";
import { clearItems } from "../actions/index";
import moment from 'moment';
import '../index.css';


class App extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            text: '',
            dueDate: ''
        }
    }

    addItem(){
        this.props.addItem(this.state.text, this.state.dueDate);
        console.log(this.state.dueDate);
    }

    deleteItem(id) {
        this.props.deleteItem(id);
    }

    renderItems() {
        const { items } = this.props;
        return (
            <ul className='list-items'>
                {
                    items.map(item => {
                        return (
                            <li key={item.id} className="list-group-item">
                                <div className="list-item">
                                    <div className="list-text">{ item.text }</div>
                                    <div className="list-date"><em>{moment(new Date(item.dueDate)).fromNow()}</em></div>
                                </div>
                                <div className="list-item list-delete-button"
                                     onClick={() => this.deleteItem(item.id)}
                                >
                                    &#x2715;
                                </div>
                            </li>
                        )
                    })
                }
            </ul>
        )
    }

    render() {
        return (
            <div className="App">

                <div className="header">

                    <div className="title">
                        Rzeczy do zrobienia:
                    </div>

                    <div className="btn btn-danger btn-delete"
                         onClick={() => this.props.clearItems() }
                    >
                        Delete all
                    </div>

                </div>

                { this.renderItems() }

                <form className="form">
                    <div className="form-group input-area">
                        <input
                            className="form-control"
                            placeholder="Pamitaj aby..."
                            onChange={event => this.setState({text: event.target.value})}
                        />

                        <input
                            className="form-control"
                            type="datetime-local"
                            placeholder='Data zakoczenia: rrrr-mm-ddThh:mm'
                            onChange={event => this.setState({dueDate: event.target.value})}
                        />
                    </div>

                    <button
                        className="btn btn-success btn-add"
                        type="button"
                        onClick={() => this.addItem()}
                    >
                        Dodaj do listy
                    </button>

                </form>

            </div>
        )
    }
}

function mapStateToProps(state){
    return {
        items: state
    }
}

export default connect(mapStateToProps, { addItem, deleteItem, clearItems })(App);


