import { ADD_ITEM} from "../constants";
import { DELETE_ITEM } from "../constants";
import { CLEAR_ITEMS } from "../constants";
import { bake_cookie, read_cookie } from 'sfcookies';

const item = (action) => {
    let { text, dueDate} = action;
    return {
        id: Math.random(),
        text: text,
        dueDate: dueDate
    }
};

const removeById = (state = [], id) => {
    const items = state.filter(item => item.id !== id);
    console.log('new reduced items', items);
    return items;
}

const items = (state = [], action) => {
    let items = null;

    state = read_cookie('items');

    switch (action.type) {
        case ADD_ITEM:
            items = [...state, item(action)];
            bake_cookie('items', items);
            return items;
        case DELETE_ITEM:
            items = removeById(state, action.id);
            bake_cookie('items', items);
            return items;
        case CLEAR_ITEMS:
            items = [];
            bake_cookie('items', items);
            return items;
        default:
            return state;
    }
};

export default items;


